<template>
  <v-row justify="center">
    <v-dialog
      :value="dialog"
      persistent
      max-width="500"
    >
      <v-card>
        <v-card-title class="text-h5">
          ÏÇ¨ÏóÖÏû• ÌèêÍ∏∞Î¨º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî
        </v-card-title>
        <v-text-field
          v-model="keyword"
          placeholder="ÏÇ¨ÏóÖÏû•ÌèêÍ∏∞Î¨º Í≤ÄÏÉâüîç"
          @input="searchSomething"
        />
        <v-row>
          <v-col>
            <v-simple-table
              :height="300"
              fixed-header
              dense
            >
              <template #default>
                <thead>
                  <tr>
                    <th class="text-center">
                      ÏÇ¨ÏóÖÏû•ÌèêÍ∏∞Î¨º
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="item,idx in cloneWsteList1"
                    :key="idx"
                    style="cursor : pointer"
                    @click="selectWsteList1(item,$event)"
                  >
                    <td>[{{ item.CODE }}] {{ item.NAME }} </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-col>
          <v-col>
            <v-simple-table
              :height="300"
              fixed-header
              dense
            >
              <template #default>
                <thead>
                  <tr>
                    <th class="text-center">
                      Ïï°ÏÉÅ Í≥†ÏÉÅ
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="item,idx in cloneWsteList2"
                    :key="idx"
                    style="cursor : pointer"
                    @click="selectWsteList2(item,$event)"
                  >
                    <td> {{ item.KOREAN }} </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-col>
        </v-row>

        <v-card-actions>
          <v-spacer />

          <v-btn
            color="green darken-1"
            text
            @click="closeDialog"
          >
            ÌôïÏù∏
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>

import {mapGetters, mapMutations} from "vuex"
export default {
  props : {
    dialog : {
      type : Boolean,
      default : false
    }},
  data(){
    return {
      cloneWsteList1 : [],
      cloneWsteList2 : [],
      keyword : '',
      timer : '',
      wste1 : null,
      wste2 : null,
      nowTarget : '',
      nowTarget2 : ''
    }
  },
  computed: {
    ...mapGetters('permit',['getWsteLists','getSelectedPermit']),

    },
  created(){
    this.cloneWsteList1 = this.getWsteLists.wsteList1
    this.cloneWsteList2 = this.getWsteLists.wsteList2
  },

    methods: {
      ...mapMutations('permit',['addWsteList']),
      setNull(){
        if (this.nowTarget) {

          this.nowTarget.classList.remove('active')
          this.nowTarget = null
        }
        if (this.nowTarget2) {

          this.nowTarget2.classList.remove('active')
          this.nowTarget2 = null
        }
        this.wste1 = null
        this.wste2 = null
      },
      closeDialog(){
        if (this.wste1 && this.wste2){

          const checkExist = this.getSelectedPermit.wsteList.filter(v => {
            return  v.WSTE_NM == this.wste1.NAME &&
                    v.WSTE_CODE == this.wste1.CODE &&
                    v.APPR_NM == this.wste2.NAME &&
                    v.APPR_CODE == this.wste2.ID
               })
          if (checkExist.length > 0){
            this.setNull()
            alert('ÎèôÏùºÌïú ÎÇ¥Ïó≠Ïù¥ Ï°¥Ïû¨Ìï©ÎãàÎã§')
            return
          }
          this.addWsteList({
            WSTE_NM : this.wste1.NAME,
            WSTE_CODE: this.wste1.CODE,
            APPR_NM: this.wste2.NAME,
            APPR_CODE :this.wste2.ID,
            })
          this.$emit('close-dialog')
        }else {
          this.$emit('close-dialog')
        }
        this.setNull()
      },
      selectWsteList1(v,event){
        if (this.wste1 == null)
        {
          event.target.classList.toggle('active')
          this.nowTarget = event.target
          this.wste1 = { NAME : v.NAME , CODE : v.CODE }
        }
        else if (v.CODE == this.wste1.CODE){
          event.target.classList.toggle('active')
          this.nowTarget = null
          this.wste1 = null
        }
        else {
          alert('ÌïòÎÇòÎßå ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî')
        }
      },
    selectWsteList2(v,event){
        if (this.wste2 == null)
        {
          event.target.classList.toggle('active')
          this.nowTarget2 = event.target
          this.wste2 = {ID : v.ID, NAME : v.KOREAN }
        }
        else if (v.ID == this.wste2.ID){
          event.target.classList.toggle('active')
          this.nowTarget2 = null
          this.wste2 = null
        }
        else {
          alert('ÌïòÎÇòÎßå ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî')
        }
      },
      searchSomething(v){
        // 1. ÌòÑÏû¨ ÏÑ†ÌÉùÎêú Í≤ÉÏù¥ ÏûàÏúºÎ©¥ Í≤ÄÏÉâÌï† Îïå ÌÅ¥ÎûòÏä§ Î¶¨Ïä§Ìä∏ ÏóêÏÑú activeÏßÄÏö∞Í≥†
        // 2. wste1ÏùÑ nullÎ°ú ÎßåÎì§Ïñ¥Ï§ÄÎã§.
        // 1. Í∑∏ Ïù¥Ïú†Îäî activeÎ•º ÏßÄÏö∞ÏßÄ ÏïäÏúºÎ©¥ ÏÑ†ÌÉùÌïòÏßÄ ÏïäÏùÄ Î∂ÄÎ∂ÑÏóêÏÑúÎèÑ Î∞±Í∑∏ÎùºÏö¥ÎìúÏÉâÏù¥ Î≥ÄÍ≤ΩÎêòÏñ¥ÏûàÍ≥†
        // 2. wste1Ïù¥ ÏûàÍ∏∞ ÎïåÎ¨∏Ïóê Îã§Î•∏ Í≤ÉÏùÑ ÏÑ†ÌÉùÌï† ÏàòÍ∞Ä ÏóÜÎã§.( ÌèêÏú†Î¶¨ Í≤ÄÏÉâ, ÏÑ†ÌÉù ÌïòÎ©¥ Ïù¥ ÌõÑÏóêÎäî Îã§Î•∏ Í≤ÉÏù¥ Ïïà ÎêòÎäî ÌòÑÏÉÅ)
        // Îî∞ÎùºÏÑú Ï¥àÍ∏∞Ìôî ÏãúÏºúÏ§ÄÎã§.
        if(this.nowTarget){
          console.log('ee')
          this.nowTarget.classList.remove('active')
          this.wste1 =null
        }
        if(this.nowTarget2){
          this.nowTarget2.classList.add('active')
        }
        // Í≤ÄÏÉâÍ∏∞Îä• Ï†ïÍ∑úÏãùÏùÑ ÏÇ¨Ïö©ÌïòÏó¨ Ìè¨Ìï®ÎêòÎäî Í∏ÄÏûêÍ∞Ä ÏûàÎäîÍ±∏ Îã§ Î≥¥Ïó¨Ï§å
        const reg = new RegExp(`${v}`)
        if(v.length != 0){
          clearTimeout(this.timer)
          this.timer = setTimeout(()=>{
            this.cloneWsteList1 = this.getWsteLists.wsteList1.filter(v => reg.test(v.NAME) )
          },500)
        }else {
          clearTimeout(this.timer)
          this.cloneWsteList1 = this.getWsteLists.wsteList1
        }

      }
    },

}
</script>
<style >
.active {
  background-color: red;
  color: white;
}
tbody tr:nth-child(odd) {
   background-color: rgba(238, 239, 243, 0.5) !important;
