<template>
  <span
    id="address-kakao1"
    class="changeAddr"
  >Ï£ºÏÜå Î≥ÄÍ≤ΩÌïòÍ∏∞</span>
</template>
<script>
import {mapMutations} from "vuex"
export default {
   mounted(){
    document.getElementById("address-kakao1").addEventListener("click", () => {
        // eslint-disable-next-line no-undef
        new daum.Postcode({
            oncomplete: (data) => {
                console.log(data)
                let fullRoadAddr = data.roadAddress; // ÎèÑÎ°úÎ™Ö Ï£ºÏÜå Î≥ÄÏàò
                let extraRoadAddr = ''; // ÎèÑÎ°úÎ™Ö Ï°∞Ìï©Ìòï Ï£ºÏÜå Î≥ÄÏàò
                // Î≤ïÏ†ïÎèôÎ™ÖÏù¥ ÏûàÏùÑ Í≤ΩÏö∞ Ï∂îÍ∞ÄÌïúÎã§. (Î≤ïÏ†ïÎ¶¨Îäî Ï†úÏô∏)
                // Î≤ïÏ†ïÎèôÏùò Í≤ΩÏö∞ ÎßàÏßÄÎßâ Î¨∏ÏûêÍ∞Ä "Îèô/Î°ú/Í∞Ä"Î°ú ÎÅùÎÇúÎã§.
                if(data.bname !== '' && /[Îèô|Î°ú|Í∞Ä]$/g.test(data.bname)){
                    extraRoadAddr += data.bname;
                } // Í±¥Î¨ºÎ™ÖÏù¥ ÏûàÍ≥†, Í≥µÎèôÏ£ºÌÉùÏùº Í≤ΩÏö∞ Ï∂îÍ∞ÄÌïúÎã§.
                if(data.buildingName !== '' && data.apartment === 'Y'){
                    extraRoadAddr += (extraRoadAddr !== '' ? ', ' + data.buildingName : data.buildingName);
                } // ÎèÑÎ°úÎ™Ö, ÏßÄÎ≤à Ï°∞Ìï©Ìòï Ï£ºÏÜåÍ∞Ä ÏûàÏùÑ Í≤ΩÏö∞, Í¥ÑÌò∏ÍπåÏßÄ Ï∂îÍ∞ÄÌïú ÏµúÏ¢Ö Î¨∏ÏûêÏó¥ÏùÑ ÎßåÎì†Îã§.
                if(extraRoadAddr !== ''){
                    extraRoadAddr = ' (' + extraRoadAddr + ')';
                } // ÎèÑÎ°úÎ™Ö, ÏßÄÎ≤à Ï£ºÏÜåÏùò Ïú†Î¨¥Ïóê Îî∞Îùº Ìï¥Îãπ Ï°∞Ìï©Ìòï Ï£ºÏÜåÎ•º Ï∂îÍ∞ÄÌïúÎã§.
                if(fullRoadAddr !== ''){ fullRoadAddr += extraRoadAddr;
                } // Ïö∞Ìé∏Î≤àÌò∏ÏôÄ Ï£ºÏÜå Ï†ïÎ≥¥Î•º Ìï¥Îãπ ÌïÑÎìúÏóê ÎÑ£ÎäîÎã§.



                Promise.resolve(data).then(()=>{
                  return new Promise((resolve,reject)=>{
                    const geocoder = new window.kakao.maps.services.Geocoder();
                    geocoder.addressSearch(fullRoadAddr,(result,status)=>{
                      if(status === window.kakao.maps.services.Status.OK){
                          const { x, y } = result[0];
                          resolve({ lat: y, log: x })
                      }else{
                          reject();
                      }
                    })
                  })
                }).then(result => {
                  let lat = parseFloat(result.lat)
                  let lng = parseFloat(result.log)
                  this.setAddr({ addr : data.address+', ', bCode : data.bcode, lat, lng } )
                })

            },
            animation : true,
            onclose: (state) => {
                if(state === 'COMPLETE_CLOSE') alert('ÏÉÅÏÑ∏ Ï£ºÏÜåÎ•º Îí§Ïóê Ïù¥Ïñ¥ÏÑú Ï†ÅÏñ¥Ï£ºÏÑ∏Ïöî!')
            },

        }).open();
    });
  },
  methods : {
    ...mapMutations('permit',['setAddr'])
  }
}
</script>
<style lang="scss">
.changeAddr{
  cursor : pointer;
  display : inline-block;
  width : 100px;
  height : 30px;
  text-align : center;
  line-height : 30px;
  border : 1px solid;
  border-radius: 10px;
  &:hover {
    background-color: burlywood;
    color: white;
  }
}
</style>
